#hr2{
	height: 4em;
	background-color: rgba(1,1,1,0);
	border-style: none;
}

#t8{
	background-color: #353535;
	overflow: auto;
	display: flex;
	justify-content: space-around;
}

#t8 img {
		transform: scale(0.65, 0.65);

}

#t8 p{
	padding-left: 4em;
	text-align: left;
	color: grey;
}

#map{
	float: right;
	position: relative;
	bottom: 2em;
	/*right: 2em;*/

}

#t8{
	padding-top: 4em;
}

#footer{
	padding: 1em 1em;
	display: flex;
	justify-content: space-between;
	background-color: #FFBC27;
}

#fbn{
	display: flex;
	width: 20%;
	justify-content: space-between;
	padding-left: 4em;
}
#fbn button img{
	transform: scale(0.75, 0.75);
}

#fbn button {
	background-color: #353535;
	border-style: solid;
	border-color: #FFBC27;
	padding: 0.2em;	
	border-radius: 60%;
	transform: scale(0.85, 0.85);

}

#fbn button:nth-of-type(1) {
	padding-right: 0.6em;
	padding-left: 0.6em;
}

#fbn button:nth-of-type(3) {
	padding-right: 0.3em;
	padding-left: 0.3em;
}

#fbn button:nth-of-type(4) {
	padding-right: 0.3em;
	padding-left: 0.3em;
}